
import React from "react";


export default class Contexto extends React.Component {

    // eslint-disable-next-line no-useless-constructor
    constructor(props) {

        super(props);

    }

    useTip() {
        this.props.ControleJogo.useTip()
        this.props.update();
    }

    createMarkup(val) {
        return { __html: (this.props.ControleJogo.state.focus + 1) + ". " + val };
    }

    
    render() {

        return (
            <div>
                {/* <h3>Tip</h3> */}
                {/* // eslint-disable-next-line */}
                <div className="modal fade " id="staticContexto"  data-bs-keyboard="false"  aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered ">
                        <div className="modal-content bg-color1 text-white">
                            <div className="modal-header border-bottom-0">
                                <button type="button" className="btn-close align-self-start p-2" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body justify-content-center">

                                <div className="contexto-incial mb-0 text-white1" dangerouslySetInnerHTML={this.createMarkup(this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus].Contexto)}></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

