
import React from "react";
import Buttons from "./SubViewComponentsJogo/teclado/Buttons";
import Gaps from "./SubViewComponentsJogo/teclado/Gaps";
import Tip from "./SubViewComponentsJogo/teclado/Tip";
import Contexto from "./SubViewComponentsJogo/teclado/Contexto";


export default class teclado extends React.Component {

    constructor(props) {
        super(props)
        this.itens = this.props.ControleJogo.Construtor.principal
        this.state = {
            gap: Array(this.itens.length).fill(0)
        }
        this.selectGap = this.selectGap.bind(this)

    }

    componentDidMount() {

        this.setState((state, props) => ({
            gap: Array(this.itens.length).fill(0)
        }));
    }

    // Linked Funcitons

    next() {

        this.props.ControleJogo.next()
        this.props.update();
    }

    prev() {
        this.props.ControleJogo.prev()
        this.props.update();
    }

    selectLetter() {
        this.props.ControleJogo.selectLetter()
        this.props.update();
    }


    // End Linked Funcitons


    selectGap(index) {

        if (this.itens[this.props.ControleJogo.state.focus].letras.length <= index)
            this.next()
        else {
            if (!this.props.ControleJogo.state.hitWord[this.props.ControleJogo.state.focus]) {
                let aux = this.state.gap;

                aux[this.props.ControleJogo.state.focus] = index
                
                this.setState((state, props) => ({
                    gap: aux
                }));
            }
        }







    }

    createMarkup(val) {
        return { __html: (this.props.ControleJogo.state.focus + 1) + ". " + val };
    }

    truncText() {
        if (this.itens[this.props.ControleJogo.state.focus].Contexto.length > 50 && window.screen.width < 700)
            return (
                <div className="ptb-2 flex-grow-1 d-flex">
                    <div className="align-self-center flex-grow-1">
                        <p data-bs-toggle="modal" data-bs-target="#staticContexto" className=" contexto-incial mb-0 text-white1" dangerouslySetInnerHTML={this.createMarkup(this.itens[this.props.ControleJogo.state.focus].Contexto.substring(0,50) + '...')}>
                        </p>
                    </div>
                    <div className="p-1 align-self-center">
                        <i data-bs-toggle="modal" data-bs-target="#staticContexto" className="material-icons md-light text-white1 text-black1 md-24">add_circle</i>
                    </div>
                </div>)
        else
            return (
                <div className="ptb-2 flex-grow-1 d-flex">
                    <div className="align-self-center" >
                        <p className=" contexto-incial mb-0 text-white1" dangerouslySetInnerHTML={this.createMarkup(this.itens[this.props.ControleJogo.state.focus].Contexto)}>
                        </p>
                    </div>
                </div>)

    }





    render() {

        return (
            <div className="flex-fill teclado  w-100 bg-color2 pt-2 arround-bl-12">

                <div className="d-flex container ">
                    <div className="p-2 align-self-center">
                        <button onClick={() => this.prev()} type="button" className="button-teclas bg-color4 buttonR teclado-buttonR"><i className="material-icons md-light text-black1 md-48 teclado-buttonR">arrow_left</i></button>

                    </div>

                    {this.truncText()}

                    <div className="p-2 align-self-center">
                        <button onClick={() => this.next()} type="button" className="button-teclas bg-color4 buttonR teclado-buttonR"><i className="material-icons md-light text-black1 md-48 teclado-buttonR">arrow_right</i></button>

                    </div>
                </div>


                <Gaps
                    ControleJogo={this.props.ControleJogo}
                    update={this.props.update}
                    ControleGap={this.selectGap}
                    StateGap={this.state}
                />

                <Buttons
                    ControleJogo={this.props.ControleJogo}
                    update={this.props.update}
                    ControleGap={this.selectGap}
                    StateGap={this.state}
                />

                <Tip
                    ControleJogo={this.props.ControleJogo}
                    update={this.props.update}
                />

                <Contexto
                    ControleJogo={this.props.ControleJogo}
                    update={this.props.update}
                />

            </div>
        );
    }
}