
import React from "react";

// Controladores

const status = {
    found : { 
        backgroundColor: 'white',
        color: 'black',
        boxShadow: 'inset 0 0 0 1px black'

    },
    none : { 
        backgroundColor: 'none',
        color: 'none'


    },
    select : { 
        backgroundColor: '#71aaff',
        color: 'black',
        boxShadow: 'inset 0 0 0 1px black'
        

    },
    hit : { 
        backgroundColor: '#0D3A7D',
        color: 'white',
        boxShadow: 'inset 0 0 0 1px black'

    }

}


export default class Cell extends React.Component {

    constructor(props) {
        
        super(props);
        this.tabuleiro = this.props.ControleJogo.Construtor.tabuleiro
    }
    
    dicaList(){
        if(this.props.ControleJogo.state.hitWord[this.props.index] || this.props.ControleJogo.state.Tips[this.props.index])
        return(
            <div className="m-2 p-2 bg-color3 arround-6"
            >
                <div className="text-center" dangerouslySetInnerHTML={this.createMarkup(this.props.ControleJogo.Construtor.principal[this.props.index].Dica)} >
                    

                </div>



            </div>

        );
    }

    renderLetter(letter){
        if (letter && letter !== -1){
            return letter
        }
           
        else
            return " "
    }

    renderCell(index) {
        return (
            <div key={index} className="flex-fill cell-lista"                 
            style={ 
                this.props.ControleJogo.state.hitWord[this.props.index] === true ?
                status.hit : 
                this.props.index === this.props.ControleJogo.state.focus ?
                status.select : 
                status.found}
            >
{this.renderLetter(this.props.ControleJogo.state.statusWords[this.props.index][index])}
            </div>

        )
    }


    renderLista(items) {
        var rowsLista = [];
        for (let index = 0; index < items.length; index++) {
            rowsLista.push(this.renderCell(index));
        }
        return React.createElement('div', { className: " d-flex w-100 p-2 m-2" }, rowsLista);
    }

    createMarkup(val) {
        return {__html:(this.props.index + 1)+"." + val};
    }


    render() {
        return (
            <div className="mx-2 p-2 my-3 bg-white1 text-center arround-6 "
            >
                <div className="fw-bolt-s" dangerouslySetInnerHTML={this.createMarkup(this.props.ControleJogo.Construtor.principal[this.props.index].Contexto)}>

                    

                </div>

                {this.dicaList()}

                <div className="d-flex w-100">
                    {this.renderLista(this.props.ControleJogo.state.statusWords[this.props.index])}
                </div>

            </div>
        );
    }
}