
import React from "react";


export default class Tip extends React.Component {

    // eslint-disable-next-line no-useless-constructor
    constructor(props) {
        /* eslint-disable */
        super(props);

    }

    useTip() {
        this.props.ControleJogo.useTip()
        this.props.update();
    }

    createMarkup(val) {
        // (this.props.ControleJogo.state.focus + 1) + ". " + val 
        return { __html: val};
    }

    renderTip() {
        if (this.props.ControleJogo.state.Tips[this.props.ControleJogo.state.focus])

            return (
                <div className="p-5 bg-white  text-black1 arround-12 text-center">
                    <h5 className="text-color1">Dica</h5>
                    <p
                    dangerouslySetInnerHTML={this.createMarkup(this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus].Dica)}>

                    </p>
                </div>
            )
        else
            return <i className="material-icons md-light text-white text-black1 md-7em">lock</i>
    }

    renderButtonTip() {
        if (this.props.ControleJogo.state.Tips[this.props.ControleJogo.state.focus])
            return ;
        else if(this.props.ControleJogo.state.tips === 0)
        return (
            <div className="modal-footer pb-4 border-top-0 text-center justify-content-center">
                <button type="button" className="button-custom inicial bg-gray1 arround-6 disabled" >Acabaram as dicas</button>
            </div>
        );
        else
        return (
                <div className="modal-footer pb-4 border-top-0 text-center justify-content-center">
                    <button type="button" className="button-custom inicial bg-color4 arround-6" onClick={() => this.useTip()}>Desbloquear dica</button>
                </div>
            );
    }

    render() {

        return (
            <div>
                {/* <h3>Tip</h3> */}
                {/* // eslint-disable*/}
                <div className="modal fade " id="staticTips"  data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered ">
                        <div className="modal-content bg-color1 text-white">
                            <div className="modal-header border-bottom-0">
                                <p className="modal-title" id="staticBackdropLabel" 
                                dangerouslySetInnerHTML={this.createMarkup((this.props.ControleJogo.state.focus + 1)+". "+ this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus].Contexto)}
                                >
                                    </p>
                                <button type="button" className="btn-close align-self-start p-2" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body justify-content-center">

                                <div className="p-2 d-flex justify-content-center align-self-center">{this.renderTip()}</div>
                            </div>

                            {this.renderButtonTip()}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}