
import React from "react";
import Item from "./SubViewComponentsJogo/lista/Item"

export default class lista extends React.Component {

    // eslint-disable-next-line no-useless-constructor
    constructor(props) {
        
        super(props);
    
    }

    renderCell(index) {
        return <Item 
            ref={React.createRef()}
            key={index+10000}
            index={index}
            ControleJogo={this.props.ControleJogo}
        />
    }


    renderLista(lin) {
        var rowsLista = [];
        for (let index = 0; index < lin; index++) {
            rowsLista.push(this.renderCell(index));
        }
        return React.createElement('div', { className: " container" }, rowsLista);
    }

    

    render() {
       
        return (
            <div className="flex-fill text-black1">
                {this.renderLista(this.props.ControleJogo.Construtor.quantPalavras)}
            </div>
        );
    }
}