
import React from "react";

// Controladores

const status = {
    found : { 
        backgroundColor: 'white',
        color: 'black',
        boxShadow: 'inset 0 0 0 1px black'

    },
    none : { 
        backgroundColor: 'none',
        color: 'none'


    },
    select : { 
        backgroundColor: '#71aaff',
        color: 'black',
        boxShadow: 'inset 0 0 0 1px black'
        

    },
    hit : { 
        backgroundColor: '#0D3A7D',
        color: 'white',
        boxShadow: 'inset 0 0 0 1px black'

    }

}


export default class Cell extends React.Component {

    constructor(props) {
        
        super(props);
        this.tabuleiro = this.props.ControleJogo.Construtor.tabuleiro
    }

    // Funções vinculadas com a Props.Jogo do componente pai

    selectFocus(index){
        if(index !== null){
            this.props.ControleJogo.selectFocus(index)
            this.props.update();
        }
    }

    componentDidMount() {
    }
  
    componentWillUnmount() {
    }

    renderLetter(letter){
        if (letter && letter !== -1){
            return letter
        }
           
        else
            return " "
    }
    
    renderId(){

        if(typeof this.tabuleiro.gabarito[this.props.index] === 'string' && this.tabuleiro.indice.includes(this.props.index) ){
            let conteudo =  this.tabuleiro.indice.indexOf(this.props.index) + 1
            return  React.createElement('span', { className: "badge badge-indice position-absolute top-0 start-0 rounded-circle  z-index-badge" },conteudo);
        }
    }


    render() {
        return (
            <div key={this.props.index} className="flex w-100 h-cell text-center align-middle position-relative "
                 style={
                     this.tabuleiro.gabarito[this.props.index] <= 0 ? 
                     status.none : 
                     this.tabuleiro.vinculo[this.props.index].length === 2 ?
                     this.props.ControleJogo.state.hitWord[this.tabuleiro.vinculo[this.props.index][0]] === true || this.props.ControleJogo.state.hitWord[this.tabuleiro.vinculo[this.props.index][1]] === true?
                     status.hit : 
                     this.tabuleiro.vinculo[this.props.index].includes(this.props.ControleJogo.state.focus) ?
                     status.select : 
                     status.found : 
                     this.props.ControleJogo.state.hitWord[this.tabuleiro.vinculo[this.props.index][0]] === true? 
                     status.hit : 
                     this.tabuleiro.vinculo[this.props.index].includes(this.props.ControleJogo.state.focus) ?
                     status.select : 
                     status.found}
                 
                 
                 
                 onClick={() => this.selectFocus(this.tabuleiro.vinculo[this.props.index][0] )}
            >
                {this.renderId()}
                {/* {  this.props.index   } */}
                {/* {Math.trunc(this.props.index/this.props.ControleJogo.ControleModo.getAltura())}  */}
                {/* {this.renderLetter(this.tabuleiro.gabarito[this.props.index]) }    */}
                {this.renderLetter(this.props.ControleJogo.state.statusBoard[this.props.index]) }
            </div>
        );
    }
}